.work-details {
  padding: 1rem 2rem !important;
}
.work-details h6 {
  font-size: 1.5rem;
  font-weight: 600;
  color: whitesmoke;
}
.work-details h3 {
  font-size: 2.5rem;
  font-weight: 700;
  color: whitesmoke;
  margin: 1rem 0;
}
.work-details p {
  font-style: italic;
  font-size: 15px;
  color: white;
  letter-spacing: 0.5px;
  line-height: 25px;
}
.work-details span.tools {
  padding: 0.5rem 1rem;
  background: whitesmoke;
  margin: 0.3rem;
  border-radius: 4px;
  font-size: 14px;
}
.work-details .link svg.svg-inline--fa.fa-external-link-alt.fa-w-16,
.work-details .link svg.svg-inline--fa.fa-github-square.fa-w-14 {
  font-size: 2rem;
  color: #fefefe;
  margin: 0 0.5rem;
  margin-top: 2rem;
  transition: 0.6s ease;
}
.link svg.svg-inline--fa.fa-external-link-alt.fa-w-16:hover,
.work-details .link svg.svg-inline--fa.fa-github-square.fa-w-14:hover {
  color: #ffff3f;
  transform: scale(1.2);
}
.work-pic .img-fluid {
  border-radius: 16px;
  width: 100%;
}

