@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
}

.loadingSpiner{
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}


.about {
  padding-top: 18rem;
  padding-bottom: 5rem;
  background-color: #fefefe;
}
.progress {
  border: 5px solid white;
  box-shadow: 0px 0px 2px gray;
  border-radius: 15px !important;
  margin-bottom: 1rem;
  height: 1.3rem !important;
}
.progress-bar {
  background: linear-gradient(to right, #766dff, #88f2ff) !important;
  border-radius: 15px;
}
.aboutMySelf h3 {
  font-size: 2.5rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 2rem;
}

.aboutMySelf p {
  font-size: 1rem;
  letter-spacing: 0.2px;
  color: gray;
  line-height: 30px;
  margin-bottom: 2rem;
}

svg.svg-inline--fa.fa-database.fa-w-14,
svg.svg-inline--fa.fa-book.fa-w-14,
svg.svg-inline--fa.fa-user-friends.fa-w-20 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: #766dff;
}

@media screen and (max-width: 576px) {
  .col-md-6.aboutMySelf.aos-init.aos-animate {
    padding: 0 3rem;
  }
  .d-flex.justify-content-between {
    flex-direction: column;
    align-items: center;
  }

  .col-md-6.progressBar.aos-animate {
    padding: 0 3rem;
    margin-top: 3rem;
  }
}

@media screen and (max-width: 992px) {
  .d-flex.aboutBtn {
    flex-direction: column;
  }
  .totalInfo {
    margin: 1rem 0;
  }
}

.card {
  border-radius: 10px !important;
  border: none !important;
  box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.08);
  transition: 0.6s ease !important;
}

.p-4.card:hover {
  box-shadow: 0px 10px 30px 20px rgba(0, 0, 0, 0.08) !important;
  -webkit-transform: scale(1.1) !important;
          transform: scale(1.1) !important;
  cursor: pointer;
}
.card-title {
  font-size: 1.4rem;
  font-weight: 700;
}
.card-text {
  font-size: 14px;
  color: #777777;
}
.card-deck a {
  width: 33%;
}

@media (max-width: 768px) {
  .card-deck a {
    width: 100%;
  }
}

