.experience {
  padding: 8rem 0;
  z-index: 100;
  background: linear-gradient(to right, #766dff, #88f2ff);
}

.toggleButtons {
  margin-bottom: 5rem;
  z-index: 10000000;
}

.line {
  height: 30rem;
  border-left: 5px solid rgba(255, 255, 255, 0.25);
}
.line.col {
  max-width: 1rem !important;
}
button.experienceBtn,
button.educationBtn {
  border: none;
  background: rgba(255, 255, 255, 0.2);
  background-blend-mode: multiply;
  color: white;
  font-size: 14px;
  width: 12rem;
  height: 3.5rem;
  border-radius: 5px;
  z-index: 100;
  transition: 0.6s ease;
}
button.active {
  outline: none;
  background-color: white;
  color: black;
}
button.experienceBtn:focus,
button.educationBtn:focus {
  outline: none;
  /* background-color: white;
  color: black; */
}

.years p {
  color: rgba(255, 255, 255, 0.8);
}
.years .firstP::after,
.secondP::after,
.thirdP::after {
  content: '';
  position: absolute;
  right: -3.4rem;
  border-radius: 50%;
  width: 0.5rem;
  height: 0.5rem;
  margin-top: 4px;
  z-index: 100;
  background-color: white;
}
.years .firstP::before,
.secondP::before,
.thirdP:before {
  content: '';
  position: absolute;
  right: -3.6rem;
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  z-index: 1;
  background: rgba(255, 255, 255, 0.3);
}
/* .experienceDetails {
  visibility: hidden;
} */

.experienceInfo h4 {
  color: white;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.experienceInfo p {
  color: rgba(255, 255, 255, 0.9);
}
.firstP {
  margin-bottom: 8rem;
}
.secondP {
  margin-bottom: 8rem;
}
.educationDetails.row {
  min-width: 70%;
  position: absolute;
  margin-top: 8.5rem;
  visibility: hidden;
}
.secondExp {
  margin: 4.2rem 0;
}

@media screen and (max-width: 576px) {
  .experience {
    padding: 2rem;
  }
  .toggleButtons {
    display: flex;
    flex-direction: column;
  }
  button.experienceBtn {
    margin: 1rem 0;
  }
  .col.py-5.years.mr-5.px-0.aos-init.aos-animate {
    margin-right: 1rem !important;
  }
  .line.col {
    padding: 0;
    height: 35rem;
  }
  .years .firstP::before,
  .secondP::before,
  .thirdP:before {
    right: -1.65rem;
    margin-top: 2.5rem;
  }
  .years .firstP::after,
  .secondP::after,
  .thirdP::after {
    right: -1.4rem;
    margin-top: 20px;
  }
  .secondExp {
    margin: 2.2rem 0;
  }
  .educationDetails.row,
  .experienceDetails.row {
    max-width: 100vw;
  }
  /* .experienceInfo.col.py-5.detailsInfo.aos-init.aos-animate {
    max-width: 100vw !important;
  } */
}
@media screen and (min-width: 416px) and (max-width: 576px) {
  .years .firstP::before,
  .secondP::before,
  .thirdP:before {
    margin-top: 1rem;
  }
  .secondExp {
    margin: 3.2rem 0;
  }
}

.header {
  padding: 1rem 2rem;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100000;
  /* width: 100vw !important; */
  max-width: 100vw !important;
  transition: all 0.5s ease-in;
}
.scrolled {
  padding: 0 2rem;
  z-index: 100000;
  background: -webkit-linear-gradient(0deg, #766dff 0%, #88f3ff 100%);
}
span.navbar-brand a {
  text-decoration: none;
}

img.logo.img-fluid {
  width: 5rem;
  cursor: pointer;
  transition: 0.5s ease-in;
}

.scrolled img.logo.img-fluid {
  width: 4rem;
}
.navbar-nav > a {
  color: white !important;
  margin: 0 1rem;
  font-weight: 500;
  text-decoration: none;
  cursor: pointer;
  letter-spacing: 0.5px;
  position: relative;
  border-bottom: 1px solid rgba(255, 255, 63, 0);
}
a.resume {
  border: none;
  background-image: linear-gradient(
    to right,
    #766dff 0%,
    #88f3ff 48%,
    #766dff 100%
  );
  background-size: 200% auto;
  color: white;
  font-weight: 600;
  font-size: 14px;
  border-radius: 5px;
  text-decoration: none !important;
  padding: 1rem 3rem;
  text-transform: uppercase;
  transition: all 0.3s ease;
}
a.resume:focus {
  outline: none;
}

.navbar-nav > a.resume:hover {
  background-position: right center;
  color: white !important;
}

a:hover {
  text-decoration: none !important;
}
.navbar-nav > a:hover {
  color: #ffff3f !important;
}
.navbar-nav > a.active {
  border-bottom: 1px solid #ffff3f;
  color: #ffff3f !important;
}

